.home {
  height: 100vh;
  background-color: #f8ef02;
}
.header {
  height: 80px !important;
  width: 100vw !important;
  top: -50vh !important;
  left: -50vw !important;
  position: absolute !important;
  align-items: center;
  vertical-align: middle;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.github-url {
  position: relative;
  left: 55%;
}

.github-url a {
  color: black;
  text-decoration: none;
  margin-right: 5px;
}

.github-url img {
  width: 30px;
  height: 30px;
}

.title {
  width: 523px;
  height: 80px;
  position: relative;
  font-size: 2.5em;
  overflow-y: visible;
  display: flex;
  color: black !important;
}
.terminal {
  position: absolute;
  top: -30vh;
  left: -50vw;
  width: 40vw !important;
  height: 390px;
  min-width: 500px;
  /* background: purple; */
  /* opacity: 0.95; */
}
.fakeButtons {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  border: 1px solid #000;
  position: relative;
  top: 6px;
  left: 6px;
  background-color: #ff3b47;
  border-color: #9d252b;
  display: inline-block;
}

.fakeMinimize {
  left: 11px;
  background-color: #ffc100;
  border-color: #9d802c;
}

.fakeZoom {
  left: 16px;
  background-color: #00d742;
  border-color: #049931;
}

.fakeMenu {
  width: 550px;
  box-sizing: border-box;
  height: 25px;
  background-color: #bbb;
  margin: 0 auto;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
}

.fakeScreen {
  background-color: #151515;
  box-sizing: border-box;
  width: 550px;
  margin: 0 auto;
  padding: 20px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

p {
  position: relative;
  left: 50%;
  margin-left: -8.5em;
  text-align: left;
  font-size: 1.25em;
  font-family: monospace;
  white-space: normal;
  overflow: hidden;
  width: 0;
}

span {
  color: #fff;
  font-weight: bold;
}

.line1 {
  color: purple;
  -webkit-animation: type 0.5s 1s steps(20, end) forwards;
  -moz-animation: type 0.5s 1s steps(20, end) forwards;
  -o-animation: type 0.5s 1s steps(20, end) forwards;
  animation: type 0.5s 1s steps(20, end) forwards;
}

.cursor1 {
  -webkit-animation: blink 1s 2s 2 forwards;
  -moz-animation: blink 1s 2s 2 forwards;
  -o-animation: blink 1s 2s 2 forwards;
  animation: blink 1s 2s 2 forwards;
}

.line2 {
  color: red;
  -webkit-animation: type 0.5s 2.25s steps(20, end) forwards;
  -moz-animation: type 0.5s 2.25s steps(20, end) forwards;
  -o-animation: type 0.5s 2.25s steps(20, end) forwards;
  animation: type 0.5s 2.25s steps(20, end) forwards;
}

.cursor2 {
  -webkit-animation: blink 1s 3.25s 2 forwards;
  -moz-animation: blink 1s 3.25s 2 forwards;
  -o-animation: blink 1s 3.25s 2 forwards;
  animation: blink 1s 3.25s 2 forwards;
  color: red;
}

.line3 {
  color: blue;
  -webkit-animation: type 0.5s 3.5s steps(20, end) forwards;
  -moz-animation: type 0.5s 3.5s steps(20, end) forwards;
  -o-animation: type 0.5s 3.5s steps(20, end) forwards;
  animation: type 0.5s 3.5s steps(20, end) forwards;
}

.cursor3 {
  -webkit-animation: blink 1s 4.5s 2 forwards;
  -moz-animation: blink 1s 4.5s 2 forwards;
  -o-animation: blink 1s 4.5s 2 forwards;
  animation: blink 1s 4.5s 2 forwards;
  color: black;
}

.line4 {
  color: #fff;
  -webkit-animation: type 0.5s 10.75s steps(20, end) forwards;
  -moz-animation: type 0.5s 10.75s steps(20, end) forwards;
  -o-animation: type 0.5s 10.75s steps(20, end) forwards;
  animation: type 0.5s 10.75s steps(20, end) forwards;
}

.cursor4 {
  -webkit-animation: blink 1s 11.5s infinite;
  -moz-animation: blink 1s 8.5s infinite;
  -o-animation: blink 1s 8.5s infinite;
  animation: blink 1s 8.5s infinite;
}

@-webkit-keyframes blink {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-moz-keyframes blink {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-o-keyframes blink {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes type {
  to {
    width: 17em;
  }
}

@-moz-keyframes type {
  to {
    width: 17em;
  }
}

@-o-keyframes type {
  to {
    width: 17em;
  }
}

@keyframes type {
  to {
    width: 17em;
  }
}
